import { injected } from "../components/wallet/connection";
import { useWeb3React } from "@web3-react/core";
import React from "react";

declare global {
  interface Window {
    ethereum: any;
  }
}

const ConnectionWallet = () => {
  const { active, activate, deactivate } =
    useWeb3React();
  const disconnect = async () => {
    console.log("disconnetti");
    try {
      await deactivate();
    } catch (err) {
      console.log(err);
    }
  };

  const connect = async () => {
    console.log("connetti");
      try {
        await window.ethereum.request({
          method: "wallet_switchEthereumChain",
          params: [{ chainId: `0x${Number(137).toString(16)}` }],
        });
      } catch (switchError: any) {
        // This error code indicates that the chain has not been added to MetaMask.

        await window.ethereum.request({
          method: "wallet_addEthereumChain",
          params: [
            {
              chainId: `0x${Number(137).toString(16)}`,
              chainName: "Polygon Mainnet",
              nativeCurrency: {
                name: "MATIC",
                symbol: "MATIC",
                decimals: 18,
              },
              rpcUrls: ["https://polygon-rpc.com/"],             
              blockExplorerUrls: ["https://polygonscan.com/"],
            },
          ],
        });
      }
      await activate(injected);
      // handle other "switch" errors
    } 
    

  return (
    <>
    {!active ? <button  style={{width:150,backgroundColor:'#ffffff', color:'#000000', border:'none', fontFamily:'Bonn', fontSize:15, fontWeight: "bold", marginLeft:10}} onClick={() => connect()}>CONNECT <br/>YOUR WALLET</button> :  <button style={{backgroundColor:'#ffffff', color:'#000000', border:'none', fontFamily:'Bonn', fontSize:15, fontWeight: "bold", marginLeft:2}} onClick={() =>disconnect()}>DISCONNECT</button>
    }
      
    </>
  );
};

export default ConnectionWallet;
