import React, {useEffect, useState} from "react";
import { LinearProgress } from "@mui/material";
import {withStyles} from "@mui/styles"
import CircularProgress from '@mui/material/CircularProgress';
import abiSALE from "../../components/abiSaleBTEM.json";
var Contract2 = require("web3-eth-contract");



const StyledLinearProgress = withStyles({
  colorPrimary: {
    backgroundColor: "#ffffff"
   
  },
  barColorPrimary: {
   
    backgroundColor: "#2DC653"
  }
})(LinearProgress);

const Progress = () => {

    const [invest, setInvest] = useState(0)
    const [percent, setPercent] = useState(0)
    const PREICO_ADDRESS = "0x7c0539Ba962ec7971C4aa4814e226720bd20D01b";
    Contract2.setProvider(window.ethereum);
    const contract = new Contract2(abiSALE, PREICO_ADDRESS);

    useEffect(()=>{
    setInterval(async() => {
        contract.methods.weiRaised().call()
            .then(
                async (x:number) => {
                var y : number = 0
                if (!!x){y = x/1000000000000000000
                setInvest(y)
                let perc = (y * 100)/50000
                setPercent(perc)
                }
            })
    },5000)
})


  return (
    <>
      <div
        style={{
          
          width: "100%",
          height: "20vh",
          display: "flex",
          flexDirection: "column",
          alignItems: "left",
          textAlign:'left', 
          alignContent:'left',
          marginLeft:25,
      
        }}
      >
        <p
          style={{
            fontFamily: "Bonn",
            fontSize: "30px",
            fontWeight: "bold",
            color: "#ffffff",
            marginLeft:3,
            marginBottom:-3,
            fontStyle:'italic',

          }}
        >

          BTEM PROGRESS PRE-ICO
        </p>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          <StyledLinearProgress
            style={{height: "4vh", width:'80%' , marginLeft:5, }}
            variant="determinate"
            value={percent}
          />
        
          <p style={{ fontFamily: "Bonn", fontWeight: "bold", fontSize:22, marginLeft: 4 , color:'#ffffff'}}>
          {(invest===0) && <CircularProgress size={20} color="success" />}{(invest>0)&& <span>{invest}</span>}/50.000 MATIC
          </p>
          
        </div>
      </div>
    </>
  );
};

export default Progress;
