import React , {useState}from 'react'
import NewsLetter from '../components/NewsLetter'
import Footer from '../components/Footer'
import SecondHeader from '../components/SecondHeader'
import Paragrafo5 from '../components/Paragrafo5'
import Paragrafo4 from '../components/Paragrafo4'
import Paragrafo3 from '../components/Paragrafo3'
import Paragrafo2 from '../components/Paragrafo2'
import Paragrafo1 from '../components/Paragrafo1'
import up from '../components/video/up.png'


const HomePage: React.FC = () => {
    const [attivo, setAttivo ] = useState(false)
    window.onscroll = function() {scrollFunction()};
  
    function scrollFunction(){
      if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
        setAttivo(true)
      }else{
        setAttivo(false)
      }
    }
    function topFunction() {
        document.body.scrollTop = 0; // For Safari
        document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
      }
    return (
        <>
            <div className='container-totale' >
               <SecondHeader />
                <Paragrafo1 />
                <Paragrafo2 />
                <Paragrafo3 />
                <Paragrafo4 />
                <Paragrafo5 />
                <NewsLetter />
                <Footer />
                <button className={attivo ? " button-up": "button-no"} onClick={topFunction}><img className="button-image" src={up} alt="arrow"/></button>

            </div>
        </>
    )
}

export default HomePage
