import React from 'react'
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';




const VerticalMenu2 = () => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event:any) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };
  
    return (
      <div style={{backgroundColor:'#1c1c1c'}}>
        <span
        
        style={{backgroundColor:'#1c1c1c'}}
          id="basic-button"
          
          aria-expanded={open ? 'true' : undefined}
          onClick={handleClick}
        >
            <p className="testo">
               NFT
          </p>
        </span>
        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            'aria-labelledby': 'basic-button',
          }}
        >
          <MenuItem style={{ marginTop:(-10),backgroundColor:'#1c1c1c', color:'#ffffff'}} onClick={handleClose}><p className="testo" >Opensea</p></MenuItem>
          <MenuItem style={{backgroundColor:'#1c1c1c', color:'#ffffff'}} onClick={handleClose}><p className="testo" >Marketplace</p></MenuItem>
          <hr className="whi" />
        </Menu>
      </div>
    );
  }


export default VerticalMenu2