import React, { useEffect, useState } from 'react'    

const Preico = () => {
    const [days, setDays] = useState(0)
    const [hours, setHours] = useState(0)
    const [minutes,setMinutes] = useState(0)
    const [seconds,setSeconds] = useState(0)
    const [days2, setDays2] = useState(0)
    const [hours2, setHours2] = useState(0)
    const [minutes2,setMinutes2] = useState(0)
    const [seconds2,setSeconds2] = useState(0)
    var countDownDate = new Date("Dec 10, 2021 18:00:00").getTime();
    var countDownDate2 = new Date("Jan 17, 2022 00:00:00").getTime();

    useEffect(()=>{
         setInterval(function () {
            var now = new Date().getTime();
            var timeleft = countDownDate - now;
           
             var days = Math.floor(timeleft / (1000 * 60 * 60 * 24));
            (days >= 0) ? setDays(days)  : setDays(0)
            var hours = Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            (hours>= 0) ? setHours(hours) : setHours(0)
            var minutes = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
            (minutes>=0) ? setMinutes(minutes) : setMinutes(0)
            var seconds = Math.floor((timeleft % (1000 * 60)) / 1000);
            (seconds>=0) ? setSeconds(seconds) : setSeconds(0)

        }, 1000);
        setInterval(function () {
            var now = new Date().getTime();
            var timeleft = countDownDate2 - now;
            var days2 = Math.floor(timeleft / (1000 * 60 * 60 * 24));
            setDays2(days2)
            var hours2 = Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            setHours2(hours2)
            var minutes2 = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
            setMinutes2(minutes2)
            var seconds2 = Math.floor((timeleft % (1000 * 60)) / 1000);
            setSeconds2(seconds2)

        }, 1000);

    })
    return(
         <div style={{ display: 'flex', flexDirection: 'column', marginTop:30, marginBottom:50}}>
             
                <div style={{ display: 'flex', justifyContent:'center',flexDirection: 'row', padding:0}}>

                     <div style={{display:'flex', flexDirection: 'column', textAlign: 'right'}}>
                            <p style={{fontSize:'13px', fontStyle: 'italic'}}>BTEM TOKEN</p>
                            <p style={{color:'rgb(45, 198, 83)',fontStyle: 'italic', fontWeight: 'bold',marginTop:(-15)}}> PRE-ICO</p>
                            <p style={{fontSize:'8px', fontStyle: 'italic', marginTop:(-20)}}>DECEMBER 10</p>
                     </div>

                     <div style={{display:'flex',width:'54vw', marginLeft:'3vw' ,backgroundColor:'#ffffff', color:'#000000', alignContent:'center', alignItems: 'center', justifyContent:'center',}}>
                     <p style={{fontSize:'35px', marginTop:0,marginBottom:0,padding:0,fontFamily:'Bonn'}}>{days}<span style={{fontSize:20, color:'rgb(45, 198, 83)'}}>d</span>{hours}<span style={{fontSize:20, color:'rgb(45, 198, 83)'}}>h</span>{minutes}<span style={{fontSize:20 , color:'rgb(45, 198, 83)'}}>m</span>{seconds}<span style={{fontSize:20 , color:'rgb(45, 198, 83)'}}>s</span></p>
                     </div>
                </div>

                 <div style={{ display: 'flex', flexDirection: 'row', marginTop:20}}>

                 <div style={{display:'flex', flexDirection: 'column', textAlign: 'right'}}>
                            <p style={{fontSize:'13px', fontStyle: 'italic'}}>BTEM TOKEN</p>
                            <p style={{color:'rgb(45, 198, 83)',fontStyle: 'italic', fontWeight:'bold',marginTop:(-15)}}> ICO</p>
                            <p style={{fontSize:'8px', fontStyle: 'italic', marginTop:(-20)}}>JANUARY</p>
                     </div>
                     <div style={{display:'flex',width:'54vw', marginLeft:'3vw' ,backgroundColor:'#ffffff', color:'#000000', alignContent:'center', alignItems: 'center', justifyContent:'center',}}>
                     <p style={{fontSize:'35px', marginTop:0,marginBottom:0,padding:0,fontFamily:'Bonn'}}>{days2}<span style={{fontSize:20, color:'rgb(45, 198, 83)'}}>d</span>{hours2}<span style={{fontSize:20 , color:'rgb(45, 198, 83)'}}>h</span>{minutes2}<span style={{fontSize:20 , color:'rgb(45, 198, 83)'}}>m</span>{seconds2}<span style={{fontSize:20 , color:'rgb(45, 198, 83)'}}>s</span></p>
                    </div>
                 </div>
            </div>
    
    )
}

export default Preico