import { Button, InputBase } from "@mui/material";
import { Box } from "@mui/system";
import { useWeb3React } from "@web3-react/core";
import React, { useState } from "react";
import { db } from "../../firebase-config";
import { collection, addDoc } from "firebase/firestore";

const FirstForm = () => {
  const { account } = useWeb3React();

  const [name, setName] = useState("");
  const [surname, setSurname] = useState("");
  const [email, setEmail] = useState("");
  const [alertsend, setAlertSend] = useState(false);
  const preIcoCollectionRef = collection(db, "preIco");

  const sendInfo = async () => {
    await addDoc(preIcoCollectionRef, {
      name: name,
      surname: surname,
      wallet: account,
      email: email,
    });
    setAlertSend(true);
  };

  return (
    <>
      <Box
        style={{
          backgroundColor: "rgb(45, 198, 83)",
          color: "#ffffff",
          fontFamily: "Bonn",
          fontSize: "20px",
          padding: "20px",
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          width: "40vw",
        }}
      >
        <p>FIRST NAME</p>
        <InputBase
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            let valore = e.target.value;
            setName(valore);
          }}
          style={{
            width: "40vw",
            backgroundColor: "#ffffff",
            textAlign: "center",
            color: "#000000",
            fontSize: "20px",
            fontFamily: "Bonn",
          }}
        />
        <p>SECOND NAME</p>
        <InputBase
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            let valore = e.target.value;
            setSurname(valore);
          }}
          style={{
            width: "40vw",
            backgroundColor: "#ffffff",
            alignItems: "center",
            color: "#000000",
            fontSize: "20px",
            fontFamily: "Bonn",
          }}
        />
        <p>EMAIL</p>
        <InputBase
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            let valore = e.target.value;
            setEmail(valore);
          }}
          style={{
            display: "flex",
            textAlign: "center",
            alignContent: "center",
            width: "40vw",
            backgroundColor: "#ffffff",
            color: "#000000",
            fontSize: "20px",
            fontFamily: "Bonn",
          }}
        />
        <p>WALLET</p>
        <Box
          style={{
            backgroundColor: "#ffffff",
            width: "40vw",
            height: "5vh",
            display: "flex",
            alignItems: "center",
            textAlign:'center',
          }}
        >
          {!!account ? (
            <p
              style={{
                color: "#000000",
                fontSize: "20px",
                textAlign:'left',
                fontFamily: "Bonn",
              }}
            >
              {account}
            </p>
          ) : (
            <p style={{ textAlign:'center',color: "#000000" }}>Wallet not connected</p>
          )}
        </Box>
        <Button
          onClick={() => sendInfo()}
          style={{
            marginTop: "40px",
            marginBottom: "25px",
            backgroundColor: "#1c1c1c",
            color: "#ffffff",
            fontFamily: "Bonn",
            width: "15vw",
            height: "10vh",
            fontSize: "20px",
            fontWeight: "bold",
          }}
        >
          Send Request
        </Button>
        {!!alertsend && (
          <p
            style={{
              color: "#000000",
              fontSize: "20px",
              width: "100%",
              fontFamily: "Bonn",
            }}
          >
            Request sent!{" "}
          </p>
        )}
      </Box>
    </>
  );
};

export default FirstForm;
